
.msg-wrap {
  width: 300px;
  padding: 2px 10px 2px 0;
}
.msg-wrap .msg-empty {
  text-align: center;
  line-height: 60px;
}
.msg-wrap .msg-item {
  width: 100%;
  padding: 5px 10px 5px 10px;
  display: inline-block;
  line-height: 20px;
}
.msg-wrap .msg-item .msg-info {
  display: inline-block;
  width: calc(100% - 12px);
  vertical-align: top;
}
.msg-wrap .msg-item .msg-info .msg-time {
  color: #5f6a75;
}
.msg-wrap .msg-item .msg-info .msg-text {
  word-break: break-all;
  display: inline-block;
}
.msg-wrap .msg-item .msg-type {
  border-radius: 50%;
  width: 8px;
  height: 8px;
  display: inline-block;
  color: #5f6a75;
}
.msg-wrap .msg-item .msg-type-notify {
  background: #5f6a75;
}
.msg-wrap .msg-item .msg-type-alert {
  background: #f00;
}


