
.bbc-notify {
  position: absolute;
  top: 0;
  color: #fff;
  max-width: 130px;
  width: 38px;
  height: 60px;
  overflow: hidden;
}
.bbc-notify i.fa {
  font-size: 1.8em;
  cursor: pointer;
  margin-top: 22px;
}
.bbc-notify i.fa:hover {
  color: #17c1c5;
}
.bbc-notify .bbc-notify-count {
  position: absolute;
  top: 12px;
  right: 0;
  width: auto;
  height: 18px;
  background: #f66;
  border-radius: 100px;
  line-height: 18px;
  text-align: center;
  font-size: 12px;
  display: inline-block;
  padding-left: 5px;
  padding-right: 5px;
}


